import React from "react"
import { graphql } from "gatsby"
import { Affix } from "antd"

import Detail from '../pages/detail'

export default function Template({ data }) {
  const { markdownRemark, site } = data
  const { siteMetadata: { gitalkConfig } } = site
  const { frontmatter, html, timeToRead, tableOfContents } = markdownRemark
  const { title, img, date, path } = frontmatter
  return (
    <Detail img={img} title={title} path={path} gitalkConfig={gitalkConfig}>
      <div className='blog-post-title'>
        <h2>{title}</h2>
        <div>{date}</div>
        <div> {timeToRead} </div>
      </div>
      <div className="blog-wrap">
        <Affix offsetTop={100}>
          <div
            className="catalog"
            dangerouslySetInnerHTML={{ __html: `<h5 style="margin-left: 10px"></h5>${tableOfContents}` }} />
        </Affix>
        <div
          className="blog-post-content"
          dangerouslySetInnerHTML={{ __html: html }}
        />
      </div>
    </Detail>
  )
}

export const pageQuery = graphql`
  query($path: String!) {
    site {
      siteMetadata {
        gitalkConfig {
          clientID
          clientSecret
        }
      }
    }
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      timeToRead
      tableOfContents(pathToSlugField: "frontmatter.path")
      frontmatter {
        date(formatString: "YYYY-MM-DD")
        path
        title
        img
      }
    }
  }
`
